        console.log(b);
        console.log(b.checkedKeys);
      },
      handleInput() {
      // 移除非数字字符
      this.inputValue = this.inputValue.replace(/\D/g, "");
      },
      someAsyncFunction(){
             console.log(`2await`);
             return new Promise((rols,rosf)=>{
                rols(`3Promise的rols`)  
             })
      },
      //async await 关键字
      example() {
          console.log('1Start');
           this.someAsyncFunction().then(res=>{
            console.log(res);
          }); 
          // 异步操作
          console.log('4After await');
          // 后续的同步代码
          console.log('5Sync code');
      },
      // 这个是对宏任务和微任务进行测试学习
      macrotask(){
            console.log('1(第一次打印:这个是触发微任务方法)(第二次打印的时候也是为了触发微任务)');
           return new Promise((rlos,rojs)=>{
            rlos()
            rojs()
           })
      },
      // 定时器
      timer(){
              console.log('2这个触发定期器的方法');
            setTimeout(()=>{
              console.log('5这个是定时器1,第一个宏任务');
              this.macrotask().then(res=>{
              console.log('6这个定时器1添加了.then微任务');
              }) 
            })
      },
      //这个是对 this.$nextTick 进行测试 
      updateMessage() {
        this.$nextTick(function () {
        this.message = `执行了this.nextTick里面的回调`
        console.log(2,this.message) // => '已更新'
        })
        this.message = '已更新'
        console.log(1,this.message) // => '未更新'
      },
      //调用插件
      DialogFn(){
        console.log(1);
        this.color="red"
        this.$ExtendFn(
