              this.dialogVisible =false
            }
        }
        )
      }
    },
   created(){
     this.macrotask().then(res=>{
      console.log('4这是.then的回调,.then是微任务');
                 setTimeout(()=>{
                           console.log('7这个是定时器2.then里面的定时器,第二个宏任务');
                 })
     })
     this.timer()    
    },
    mounted(){
      console.log('3这个mouted');  
    },
    updated(){
      // console.log('这个updated');
    },
    directives:{
      //第一种写法
        color:{
      //el代表的是书写自定义指令的元素，binding类似于事件对象
          bind(el,binding){
      //  el.style.color="red"
        el.style.color=binding.value
          },
        update (el,binding){
        el.style.color=binding.value
        console.log('这个是自定义指令变化的时候进来的');
      //  bind 函数只调用 1 次：当指令第一次绑定到元素时调用，当 DOM 更新时bind 函数不会被触发。 update 函 数会在每次 DOM 更新时被 
      }
      },
      //第二种写法
      coloc(el,binding){
          //如果 bind 和update 函数中的逻辑完全相同，则对象格式的自定义指令可以简写成函数格式：
          el.style.color="red"
        },
      clipboard: {
      inserted(el, binding) {
        el.addEventListener('click', () => {
          const text = binding.value;
          navigator.clipboard.writeText(text)
            .then(() => {
              console.log('已成功复制文字:', text);
              // 这里可以根据需要进行额外的处理，例如显示一个提示
            })
            .catch((error) => {
              console.error('复制文字失败:', error);
              // 这里可以根据需要进行错误处理，例如显示一个错误提示
            });
        });
      }
    }
      }

  }


</script>
 
